.bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  max-width: 100%;
  background-size: cover;

  &--clouds {
    @include make-adaptive-bg("../img/bg/bg-clouds", "png");

    top: -215px;
    height: 1513px;

    @include mobile {
      height: 500px;
      top: 0;
    }
  }

  &--top-gradient {
    height: 800px;
    background-image: linear-gradient(180deg, rgba($color: $color-black-pearl, $alpha: 0.8) 0%, rgba($color: $color-black-pearl, $alpha: 0.5) 55%, rgba($color: $color-black-pearl, $alpha: 0.0) 100%);
    z-index: $layer-2;

    @include mobile {
      height: 400px;
    }
  }

  &--mnts {
    @include make-adaptive-bg("../img/bg/bg-mnts", "png");

    top: 470px;
    height: 1160px;

    @include mobile {
      top: 200px;
      height: 450px;
    }
  }

  &--hill {
    @include make-adaptive-bg("../img/bg/bg-hill", "png");

    top: 725px;
    height: 926px;

    &::before {
      @include pseudo;

      bottom: 0;
      left: 0;
      width: 100%;
      height: 300px;
      background: linear-gradient(0deg, $color-black-pearl, $color-transparent);
    }

    @include mobile {
      top: 356px;
      height: 300px;
    }
  }
}
